
 
 .rowDiv {
    align-items: center;
    border-radius: 6px;
    margin: 1rem auto;
   
   width: 80%;
   animation-name: quotesItem;
 animation-duration: 1s;
    
   
 }
 
 .newBlockquote {
    color: rgba(19, 17, 15, 0.6);
    font-family: Tahoma, sans-serif;
    font-size: 40px;
    
    margin: 0 auto;
    align-items: center;
    
    word-wrap: break-word;
    display: block;
 
 }
 .newBlockquote h1 {
    font-size: 4rem;
 }
 .newBlockquote p {
    font-style: italic;
    margin-bottom: 0;
 }
 
 .newBlockquote p::before,
 .newBlockquote p::after {
    content: "“";
    font-family: Georgia;
    font-size: 8rem;
    margin: -2rem 0 0 -4rem;
    position: absolute;
    opacity: 0.5;
 }
 
 .newBlockquote p::after {
    content: "”";
    margin: -1rem -4rem 0 0;
    
 }
 
 .newBlockquote cite {
    font-size: 1.5rem;
 }
 
 


@keyframes quotesItem {
   0%   {opacity:  0%;}
   25%  {opacity:  25%;}
   50%  {opacity:  50%;}
   100% {opacity:  100%;}
   
}